body{
	background:  #ececec;
	
}
.container{
	display:flex;
	align-items:center;
	justify-content:center;	
	flex-direction: column;
}
.image{
	width:200px;
	margin-top:20px;
	transition: 0.4s;
	cursor: pointer;
}
.image:hover{
	 transform: scale(1.2);
}
@media (max-width:770px){
	.container{
		margin:0 10%;
	}
.image{
	width:100%;
}
}
p{
	font-size:20px;
	font-family: "Lucida Console", Courier, monospace;
	text-align: center;
	transition: 0.2s;
}
p:hover{
	transform: skewX(5deg);
}